body {
  background-color:#300c00;
  width:100%;
}

.carousel-indicators   {
  bottom:-5px;
}

.carousel-indicators-numbers {
  li {
    display:none;
    text-indent: 0;
    margin: 0 2px;
    width: 20px;
    height: 20px;
    border: none;
    border-radius: 100%;
    line-height: 20px;
    color: #fff;
    background-color: #999;
    transition: all 0.25s ease;
    &.active, &:hover {
      margin: 0 2px;
      width: 20px;
      height: 20px;
      background-color: #337ab7;
    }
  }
}

.noborderradius {
  -webkit-border-radius: 0 !important;
  -moz-border-radius: 0 !important;
  border-radius: 0px !important;
  border:0px;
  height:73px;
}

.navbar-brand,.navbar-header {
  padding:0px;
  margin:0px;
  height:71px;
}

.navbar-nav > li > a {
  height: 71px;
  line-height:71px;
  padding:0px;
  padding-left:15px;
  padding-right:15px;
}

@media (max-width: 1000px) {

  .navbar-header {
    float: none;
  }

  .navbar-left,.navbar-right {
    float: none !important;
  }
  .navbar-toggle {
    display: block;
  }
  .collapse, .navbar-collapse {
    border-top: 1px solid transparent;
    box-shadow: inset 0 1px 0 rgba(255,255,255,0.1);
  }
  .navbar-fixed-top {
    top: 0;
    border-width: 0 0 1px;
  }
  .navbar-collapse.collapse {
    display: none!important;
    margin-left:-15px;
    margin-right:-15px;
    padding:0px;
  }
  .navbar-nav.nav {
    float: none!important;
    margin:0px;
  }
  .navbar-nav>li {
    float: none;
    background-color: #222;
  }
  .navbar-nav>li>a {
    height:45px !important;
    line-height:45px !important;
  }

  .collapse.in{
    display:block !important;
  }
  .navbar-nav .open .dropdown-menu {
    position: static;
    float: none;
    width: auto;
    margin-top: 0;
    background-color: transparent;
    border: 0;
    -webkit-box-shadow: none;
    box-shadow: none;
  }

  .dropdown-menu > li > a {
    background-color: transparent;
    line-height: 20px;
    color: #9D9D9D;
  }

  .dropdown-menu > li > a:hover {
    background-color: transparent;
    line-height: 20px;
    color: white;
  }
}

@media (max-width: 500px) {

  .col-sm-2 {
    text-align: center;
  }

  .col-sm-2 > div  {
    display:inline-block;
  }
}

.navbar .nav > li {
  z-index: 10000;
}

.carousel, .carousel-inner > .item {
  background-color:#eaba00;
  height:auto !important;
  min-width:100%;
}

.imgholder {
  width:40%;
  float:left;
  max-width: 350px;
  padding:5px;
}

#myCarousel {
